<template>
  <div class="student-manage">
    <el-card><h4>学员管理</h4></el-card>
    <!-- class="table"设置一个公共的css -->
    <el-card class="table">
      <div class="filter-section">
        <!-- 选择器 -->
        <!--   v-model="queryParams.kindName" 选择分类 
                clearable是否可以清空选项
               filterable Select组件是否可筛选
               change选中的值发生变化时  回调参数val目前选中的值
        -->
        <!-- 课程分类选项 -->
        <el-select
          v-model="queryParams.kindName"
          class="filter"
          placeholder="请选择项目"
          clearable
          filterable
          @change="kindNameChanged"
        >
          <!-- 选择项 courseObj.kindName遍历课程分类
          label用户看到的,value显示的值
          -->
          <el-option
            v-for="item in courseObj.kindName"
            :key="item.value"
            :label="item.value"
            :value="item.value"
          />
        </el-select>

        <!-- 课程名称 -->
        <el-select
          v-model="queryParams.courseName"
          class="filter"
          placeholder="请选择课程名称"
          clearable
          filterable
          @change="getStudentData"
        >
          <el-option
            v-for="item in courseName"
            :key="item.value"
            :label="item.value"
            :value="item.value"
          />
        </el-select>

        <!-- 输入班期名称 -->
        <el-input
          v-model="queryParams.className"
          class="filter"
          clearable
          placeholder="请输入班期名称"
          @change="getStudentData"
        ></el-input>
      </div>
      <!--  :max-height="tableHeight"超过最大高度形成滚动条 -->
      <el-table
        :data="studentList"
        v-loading="loading"
        :max-height="tableHeight"
      >
        <el-table-column prop="phoneNumber" label="手机号码" width="200" />
        <el-table-column prop="courseName" label="课程名称" width="200" />
        <el-table-column prop="className" label="班期名称" width="200" />
        <el-table-column prop="origin" label="学员来源" width="200" />
        <el-table-column label="是否启用" width="200">
          <template #default="{ row }">
            <!-- 激活的文字描述active-text , :before-change切换之前的操作-->
            <el-switch
              v-model="row.isEnable"
              active-color="#13ce66"
              active-text="启用"
              :before-change="isEnableChanged(row)"
            />
          </template>
        </el-table-column>
        <el-table-column prop="updatePeople" label="更新人" width="200" />
        <el-table-column label="更新时间" width="200">
          <template #default="{ row }">
            <span>{{ formatTime(row.updateTime) }}</span>
          </template>
        </el-table-column>
        <!--  align="center"居中  fixed="right"列固定到右侧-->
        <el-table-column label="操作" width="300" fixed="right" align="center">
          <!--scope.row==> {row}解构当前的数组项对象 -->
          <template #default="{ row }">
            <el-button size="small" @click="openDetail(row)"
              >学员详情</el-button
            >
            <el-button size="small" @click="openClass(row)">选择班期</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页器 -->
      <!-- size-change页数发生变化时触发的函数,current-change页码发生变化触发的函数 
       @size-change="getStudentData"  @current-change="getStudentData"重新获取数据
      -->
      <el-pagination
        v-model:current-page="queryParams.currentPage"
        v-model:page-size="queryParams.pageSize"
        :page-sizes="[10, 20, 50, 100]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        @size-change="getStudentData"
        @current-change="getStudentData"
        class="pagination"
      />
    </el-card>
    <!--  :studentObj="studentObj"父传子 -->
    <StudentDetail
      ref="studentDetailRef"
      :studentObj="studentObj"
      @change="getStudentData"
    ></StudentDetail>
    <StudentClass
      :studentClassObj="studentClassObj"
      ref="studentClassRef"
      @change="getStudentData"
    ></StudentClass>
  </div>
</template>

<script setup>
import { computed, onActivated, ref } from "vue";
import { getStudentList, getCourseObj, reviseIsEnable } from "@/api/student";
import { ElMessage } from "element-plus";
import { formatTime } from "@/utils/formatTime";
import { getAutoHeight } from "@/hooks/getAutoHeight";
import StudentDetail from "./components/StudentDetail.vue";
import StudentClass from "./components/StudentClass.vue";

//定义前端筛选项
const queryParams = ref({
  kindName: "", // 分类
  courseName: "",
  className: "",
  currentPage: 1,
  pageSize: 10,
});

const studentList = ref([]); //预设的筛选数组
const total = ref(0); //总数据
const courseObj = ref({}); //定义预设课程分类对象
const loading = ref(true);
const studentDetailRef = ref(null);
const studentClassRef = ref(null);

const studentObj = ref({});//学生对象(保存当前行的数据)
const studentClassObj = ref({});

const { tableHeight } = getAutoHeight(340);

// 获取学员列表函数
const getStudentData = async () => {
  loading.value = true;
  //  const { data } 解构后端返回的数据
  const { data } = await getStudentList(queryParams.value); //把数据传送给后端
  studentList.value = data.data; //把筛选项的结果数据赋值给 studentList
  total.value = data.total; //把总数据赋值给total
  setTimeout(() => {
    loading.value = false;
  }, 500);
};
// 获取分类课程对象函数
const getCourse = async () => {
  const { data } = await getCourseObj();
  courseObj.value = data; //分类课程对象赋值给 courseObj
};

getStudentData();
getCourse();
// 计算属性计算出课程名称
const courseName = computed(() => {
  const kindName = queryParams.value.kindName; //获取当前的课程分类
  const courseObjValue = courseObj.value; //获取课程名称
  // 通过课程分类来限制课程名称的选择项
  if (!kindName) {
    // 课程分类第一个未筛选时
    return courseObjValue.courseName; //整个个课程
  } else {
    // 课程分类有筛选时
    // 返回找出与之获取课程分类相同的数组项的课程分类的课程名称
    return courseObjValue.kindName.find((item) => item.value === kindName)
      .courseName;
  }
});

// 课程分类选项数据发生变化时 value为选中的数据
const kindNameChanged = (value) => {
  if (value) {
    // 拿出 kindName数组中对应value(前端/后端)的数组项中的 courseName后面的value是课程类别
    const courseName = courseObj.value.kindName.find(
      (item) => item.value === value
    ).courseName;

    // 判断第二个筛选框中的课程 是否属于第一个筛选框选中的 项目类别
    const obj = courseName.find(
      (item) => item.value === queryParams.value.courseName
    );

    if (!obj) {
      // 不匹配 要清空第二个筛选框
      queryParams.value.courseName = "";
    }
  }
  getStudentData(); //重新获取学员列表函数
};

// 切换是否启用按钮
// reviseIsEnable(row);传入row用于是否开启按钮
const isEnableChanged = (row) => {
  return async () => {
    const { data } = await reviseIsEnable(row);
    if (!data.code) {
      ElMessage.success(data.txt);
      return true; // 允许按钮修改
    } else {
      ElMessage.error(data.txt);
      return false; // 不允许按钮修改
    }
  };
};
// 打开学员详情
const openDetail = (row) => {
  studentObj.value = row;//把当前行的数据赋值给 studentObj
  studentDetailRef.value.open();
};
// 打开选择班期
const openClass = (row) => {
  // console.log(courseName.value);
  const classOptions = courseName.value.find(
    (item) => item.value === row.courseName
  ).className;

  studentClassObj.value = {
    phoneNumber: row.phoneNumber,
    className: row.className,
    courseName: row.courseName,
    classOptions,
  };

  studentClassRef.value.open();
};
onActivated(getStudentData);//失活时onActivated
</script>

<style lang="scss" scoped></style>
